<template>
  <!-- 日报详情里的弹框 -->
  <div class="container">
    <el-dialog title="关联业务" v-model="dialogVisible" width="560px">
      <div>
        <div class="menu_details_line">
          <span>业务</span>
          <el-select
            size="mini"
            style="width: 100%"
            v-model="businessId"
            placeholder="请选择"
          >
            <el-option
              v-for="item in businessList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <div class="icon">
            <i>*</i>
          </div>
        </div>
        <div class="menu_details_line">
          <span>客户</span>
          <el-select
            size="mini"
            style="width: 100%"
            v-model="customerId"
            placeholder="请选择"
          >
            <el-option
              v-for="item in customerList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <div class="icon">
            <i>*</i>
          </div>
        </div>
        <div class="menu_details_line">
          <span>业务单</span>
          <el-select
            size="mini"
            style="width: 100%"
            v-model="businessorderId"
            placeholder="请选择"
          >
            <el-option
              v-for="item in businessorderList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <div class="icon">
            <i>*</i>
          </div>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button size="mini" type="primary" @click="dialogVisible = false"
            >保 存</el-button
          >
          <el-button size="mini" @click="dialogVisible = false"
            >取 消</el-button
          >
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { reactive, toRefs } from "@vue/reactivity";
import { getCurrentInstance } from "@vue/runtime-core";

export default {
  setup() {
    const state = reactive({
      businessId: '',
      businessList: [],
      customerId:'',
      customerList:[],
      businessorderId:'',
      businessorderList:[],
      dialogVisible: false,

    });
    const { proxy } = getCurrentInstance();
    const setshow = () => {
      proxy.dialogVisible = true;
    };
    const setedit = () => {
      proxy.dialogVisible = true;
    };
    return {
      setshow,
      setedit,
      ...toRefs(state),
    };
  },
};
</script>
<style scoped>
.menu_details_line {
  display: flex;
  align-items: center;
  margin-top: 10px;
  width: 490px;
}
.icon {
    color: red;
}
.menu_details_line > span {
  width: 100px;
  text-align: right;
  margin-right: 5px;
}
</style>
