        <div class="code">
          <small>报告编号</small>
          <div class="code_box">
            <el-input
              size="mini"
              v-model="report_number"
              placeholder=""
            ></el-input>
          </div>
          <div class="icon">
            <i>*</i>
          </div>
        </div>
        <div class="code">
          <small>报告主题</small>
          <div class="code_box">
            <el-input
              size="mini"
              v-model="report_subject"
              placeholder=""
            ></el-input>
          </div>
          <div class="icon">
            <i>*</i>
          </div>
        </div>
      </div>
      <div class="item_two">
        <div class="code">
          <small>报告人</small>
          <div class="code_box">
            <el-select
              size="mini"
              placeholder=""
              class="newselect"
              v-model="reporterId"
            >
              <el-option
                v-for="item in reporterlist"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
          <div class="icon">
            <i></i>
          </div>
        </div>
        <div class="code">
          <small>报告日期</small>
          <div class="code_box">
            <el-date-picker
              style="width: 100%"
              size="mini"
              v-model="report_date"
              type="date"
              placeholder="选择日期"
            >
            </el-date-picker>
          </div>
          <div class="icon">
            <i></i>
          </div>
        </div>
      </div>

      <div class="item_two">
        <div class="code">
          <small>工作内容</small>
          <div class="code_box">
            <el-input size="mini" v-model="job_content"></el-input>
          </div>
          <div class="icon">
            <i></i>
          </div>
        </div>
        <div class="code">
          <small>备注</small>
          <div class="code_box">
            <el-input
              type="textarea"
              :rows="1"
              size="mini"
              v-model="remarks"
            ></el-input>
          </div>
          <div class="icon">
            <i></i>
          </div>
        </div>
      </div>

      <div class="item_two line_box">
        <div class="code">
          <small>创建人</small>
          <div class="code_box"><small>王立</small></div>
          <div class="icon">
            <i></i>
          </div>
        </div>
        <div class="code">
          <small>创建时间</small>
          <div class="code_box">
            <small> 2021-09-09</small>
            <small>17:30</small>
          </div>
          <div class="icon">
            <i></i>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <div class="footer_title">
        <p>关联业务</p>
        <el-button type="primary" size="mini" @click="addBtn()">添加</el-button>
      </div>
      <div class="power_main">
        <el-table
          :data="tableData"
          border
          style="width: 100%"
          size="mini"
          :height="$store.state.innerheight - 420"
          :header-cell-style="{ background: '#f5f5f5' }"
        >
          <el-table-column fixed type="index" label="序号" width="80">
          </el-table-column>
          <el-table-column prop="business" label="业务" width="">
          </el-table-column>
          <el-table-column prop="customer" label="客户" width="">
          </el-table-column>
          <el-table-column prop="business_order" label="业务单" width="">
          </el-table-column>
          <el-table-column label="操作" width="">
            <template #default="scope">
              <el-button @click="handleEdit(scope.row)" type="text" size="mini"
                >编辑</el-button
              >
              <el-button
                style="color: red"
                type="text"
                size="mini"
                @click="handleDel(scope.row)"
                >移除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
