<script>
import { reactive, toRefs } from "vue";
import { getCurrentInstance } from "@vue/runtime-core";
import dailyInformationDialog from "../daily_management/dailyInformationDialog.vue";
export default {
  components: {
    dailyInformationDialog,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      reporterlist: [],
      rolelist: [],
      reporterId: "",
      report_number: "",
      report_subject: "",
      report_date: "",
      job_content: "",
      remarks: "",
      tableData: [
        {
          business: "跟进-电话",
          customer: "客户",
          business_order: "跟进",
        },
      ],
    });
    const addBtn = () => {
      proxy.$refs.dailyInformationDialog.setedit();
    };
    const handleEdit = (res) => {
      proxy.$refs.dailyInformationDialog.setedit(res);
    };
    const handleDel = () => {
      console.log();
    };
    return {
      ...toRefs(state),
      handleEdit,
      handleDel,
      addBtn,
    };
  },
};
</script>

