.contain {
  margin: 10px;
  border: 1px solid #e0e0e0;
  width: 1000px;
}
.footer {
  margin: 10px;
  border: 1px solid #e0e0e0;
  width: 1000px;
}
.power_main {
  padding: 0px 10px;
  box-sizing: border-box;
}
.item_header {
  font-size: 15px;
  font-weight: bold;
  padding: 10px;
  box-sizing: border-box;
}
.footer_title {
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}
.footer_title > p {
  font-size: 15px;
  font-weight: bold;
  margin: 0;
}

.item_two {
  display: flex;
  align-items: center;
  padding-top: 10px;
}

.code_box {
  width: calc(100% - 120px);
}
.code {
  width: 49%;
  display: flex;
  align-items: center;
}
.code > small {
  width: 80px;
  text-align: right;
  margin-right: 5px;
}
.icon {
  color: red;
}
.line_box {
  margin-bottom: 10px;
}
</style>
